// ['activeTab', 'menuToggle', 'leftToggle']
export default {
  name: 'RightMenu',
  props: {
    activeTab: {
      type: Number,
      default: 0
    },
    menuToggle: {
      type: Boolean,
      default: false
    },
    leftToggle: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      // 右侧tab标签名称
      tabName: [{
        name: '消息',
        icon: 'el-icon-message-solid'
      }, {
        name: '菜单',
        icon: 'el-icon-menu'
      }]
    }
  },
  methods: {
    // 右侧tab栏选中函数
    activeHandle (index) {
      this.activeTab = index
    },

    // 控制父级组件的左侧导航栏的收起和展开的点击事件
    AsideToggleHandle () {
      // 将控制左侧导航栏开关的变量传递回给父级组件
      this.$emit('leftToggle')
    }
  }
}
