import RightMenu from '@/components/rightMenu/rightMenu'
export default {
  name: 'Home',
  components: {
    RightMenu // 右侧菜单栏组件
  },
  data () {
    return {
      // 控制左侧侧边栏的收齐和展开
      collapse: false,

      // 右侧已经打开过的导航标签的数组
      asidesItems: [
        {
          url: '/',
          tagname: '首页',
          active: true // 是否处于选中状态标签
        } // 默认含有一个首页标签
      ],

      // 控制右侧菜单栏的收起和展开
      menuToggle: false, // 默认关闭

      // 控制右侧tab栏选中标签的索引值
      activeTab: 0 // 设置默认值为0

    }
  },
  methods: {
    // 控制侧边栏显示和隐藏的函数
    AsideToggle () {
      this.collapse = !this.collapse
    },

    // 左侧导航栏选中函数
    asideSelect (key) {
      // 先将asidesItems中的控制是否选中全都设置成false
      this.asidesItems.forEach((item) => {
        item.active = false
      })

      const tag = {}
      tag.url = '/' + key.split('/')[1]
      tag.tagname = key.split('/')[2]
      tag.active = true // 因为是点选中，所以要设置为选中状态
      // 判断导航标签的数组中是否存在该索引值
      const isExistence = this.asidesItems.find((item) => {
        return item.url === tag.url
      })
      if (!isExistence) {
        this.asidesItems.push(tag)
      }

      // 修复了isExistence出现未定义的bug
      if (isExistence) {
        // 数组中存在这个索引值的情况找到数组中这歌元素改为选中状态
        isExistence.active = true
      }
      return false
    },
    // tag标签点击跳转函数
    tagClick (item) {
      const url = item.url

      this.$router.push('/Home' + url)

      // 重新设置选中状态
      // 先将asidesItems中的控制是否选中全都设置成false
      this.asidesItems.forEach((item) => {
        item.active = false
      })

      const activeTag = this.asidesItems.find((item) => {
        return item.url === url
      })

      // 设置为选中状态
      activeTag.active = true
    },

    // tag标签关闭函数
    tagClose (item) {
      // 从列表中删除这个标签
      const url = item.url
      const tagIndex = this.asidesItems.findIndex((item) => {
        return item.url === url
      })
      // 根据索引值进行删除
      this.asidesItems.splice(tagIndex, 1)

      // 重新规定选中的标签,选中最后一个标签
      const activeTag = this.asidesItems[this.asidesItems.length - 1]
      // 调用选中函数
      this.tagClick(activeTag)
    },

    // 显示右侧栏的函数
    showMenu (index) {
      this.menuToggle = !this.menuToggle
      // 传递索引值
      this.activeTab = index
    }

  }

}
